import React, { useState, useEffect } from "react";

import {
    Dialog, DialogTitle, DialogContent,
    DialogActions,
    Button, withMobileDialog, 
    makeStyles, Grid,
    Select, MenuItem, FormControl, InputLabel
} from "@material-ui/core";

import moment from "moment";

import { getAllEarthquakeIntensityForThisDay } from "../ajax";
import { SlideTransition, FadeTransition } from "../../reusables/TransitionList";

const useStyles = makeStyles(theme => ({
    inputGridContainer: {
        marginTop: 6,
        marginBottom: 6
    },
    checkboxGridContainer: {
        marginTop: 12,
        marginBottom: 6
    },
    selectInput: {
        width: "auto",
        [theme.breakpoints.down("xs")]: {
            width: "250px"
        }
    },
    root: {
        width: "90%",
    },
    backButton: {
        marginRight: 1
    },
    instructions: {
        marginTop: 1,
        marginBottom: 1,
    },
    link: { textDecoration: "none" }
}));

function EarthquakeIntensityTemplateModal (props) {
    const {
        fullScreen, isOpen,
        clickHandler, setComposedMessage
    } = props;

    const classes = useStyles();
    const [eq_intensity_list, setEqIntensityList] = useState([]);
    const [selected_eq_intensity, setSelectedEqIntensity] = useState(0);


    const [is_disabled, setIsDisabled] = useState(true);
    useEffect(() => {
        setIsDisabled(selected_eq_intensity === null);
    }, [selected_eq_intensity]);

    useEffect(() => {
        const payload = {
            start_ts: moment().format("YYYY-MM-DD 00:00:00"),
            end_ts: moment().format("YYYY-MM-DD 23:59:59")
        };
        
        getAllEarthquakeIntensityForThisDay(payload, data => {
            setEqIntensityList(data);
        }, error => {
            console.log(error);
        });
    }, []);

    const generateTemplate = () => {
        // eslint-disable-next-line radix
        const int_val = parseInt(selected_eq_intensity);
        setSelectedEqIntensity(selected_eq_intensity);
        const selected = eq_intensity_list.filter(e => e.mei_id === int_val);
        
        if (selected.length > 0) {
            const { intensity, reporter, ts } = selected[0];
            const { first_name, last_name, organizations } = reporter;
            const { site: { barangay, province, municipality } } = organizations[0];
            const timestamp = moment(ts).format("LLL");
    
            const template = `-${first_name} ${last_name} \n-${timestamp} \n-Brgy. ${barangay}, ${municipality}, ${province} \n-Intensity ${intensity}`;
            setComposedMessage(template);
            clickHandler();
        }
    };

    return (
        <Dialog
            fullWidth
            fullScreen={fullScreen}
            open={isOpen}
            aria-labelledby="form-dialog-title"
            TransitionComponent={fullScreen ? SlideTransition : FadeTransition}      
        >
            <DialogTitle id="form-dialog-title">
                Select earthquake intensity data to generate
            </DialogTitle>
            <DialogContent>
                <Grid
                    container 
                    spacing={1} 
                    alignItems="center"
                    justify="space-evenly"
                >
                    <Grid item xs={12} className={classes.inputGridContainer}>
                        <FormControl fullWidth>
                            <InputLabel id="eq-event-select-label">Select Earthquake Intensity</InputLabel>
                            <Select
                                labelId="eq-event-select-standard-label"
                                id="eq-event-select-standard"
                                value={selected_eq_intensity}
                                onChange={e => setSelectedEqIntensity(e.target.value)}
                                label="Earthquake Intensity"
                            >
                                <MenuItem value="0">
                                    <em>None</em>
                                </MenuItem>
                                {
                                    eq_intensity_list.length > 0 && (
                                        eq_intensity_list.map((row, index) => {
                                            const { intensity, reporter, ts, mei_id } = row;
                                            const { first_name, last_name, organizations } = reporter;
                                            const { site: { barangay, province, municipality } } = organizations[0];
                                            const timestamp = moment(ts).format("LLL");
                                            return (
                                                <MenuItem value={`${mei_id}`} key={index}>{first_name} {last_name} | {timestamp} | Brgy. {barangay}, {municipality}, {province} | Intensity {intensity}</MenuItem>
                                            );
                                        })
                                    )
                                }
                            </Select>
                        </FormControl> 
                    </Grid>
                </Grid>
            </DialogContent>
               
            <DialogActions>
                <Button color="primary" onClick={generateTemplate} disabled={is_disabled}>
                    Generate
                </Button>
                <Button onClick={clickHandler}>
                    Cancel
                </Button>
            </DialogActions>
        </Dialog>
    );
}

export default withMobileDialog()(EarthquakeIntensityTemplateModal);
