import React, { Fragment, useState, useEffect } from "react";
import {
    Grid, TextField,
    FormControl,
    Select, MenuItem, InputLabel
} from "@material-ui/core";

import MomentUtils from "@date-io/moment";
import { MuiPickersUtilsProvider, KeyboardDateTimePicker } from "@material-ui/pickers";
import moment from "moment";
import { getEarthquakeAlerts } from "./ajax";
import { getEarthquakeEventsForLast24hrs } from "../on_demand/ajax";

// Widgets
import DynaslopeUserSelectInputForm from "../../reusables/DynaslopeUserSelectInputForm";
import DynaslopeSiteSelectInputForm from "../../reusables/DynaslopeSiteSelectInputForm";

function EarthquakeIntensityForm (props) {
    const {
        site,
        setSite,
        selectSite,
        reporter,
        setReporter,
        remarks,
        intensity,
        setRemarks,
        setEqEvent,
        setIntensity,
        alertsFromDbData,
        eqId,
        setEqId,
        tsFelt,
        setTsFelt
    } = props;

    const [earthquake_events, setEarthquakeEvents] = useState([]);
    const handleIntensity = (event) => {
        setIntensity(event.target.value);
    };

    const handleRemarks = (event) => {
        setRemarks(event.target.value);
    };

    const handleEarthquakeEvent = (event) => {
        const event_id = event.target.value;
        setEqId(event_id);
        setEqEvent(earthquake_events.filter(e => e.event_id === event_id));
    };

    const handleTimestampFelt = (event) => {
        setTsFelt(event);
        console.log(event);
    };
    
    useEffect(() => {
        if (site !== null) {
            const site_id = site.value;
            const { latest, overdue } = alertsFromDbData;
            const all_alerts_from_db = [];
            // eslint-disable-next-line array-callback-return
            latest.map((row, index) => {
                all_alerts_from_db.push(row);
            });
            
            // eslint-disable-next-line array-callback-return
            overdue.map((row, index) => {
                all_alerts_from_db.push(row);
            });
            getEarthquakeAlerts(return_data => {
                console.log(return_data);
                const earthquake_alerts = return_data.data;
                const temp_arr = [];
                const site_ids = [];
                earthquake_alerts.map((row, index) => {
                    const { eq_alerts } = row;
                    const search_data = eq_alerts.filter(e => e.site_id === site_id);
                    if (search_data.length > 0) {
                        if (!site_ids.includes(site_id)) {
                            let temp_event_data = null;
                            temp_event_data = all_alerts_from_db.filter(e => e.event.site_id === site_id);
                            
                            if (temp_event_data.length > 0) {
                                site_ids.push(site_id);
                                row.eq_alerts = search_data[0];
                                row.event_id = temp_event_data[0].event.event_id;
                                temp_arr.push(row);
                            }
                        }
                    }

                    return temp_arr;
                });

                const json_data = {
                    start_ts: moment().format("YYYY-MM-DD HH:mm:ss"),
                    end_ts: moment().subtract(1, "days")
                    .format("YYYY-MM-DD HH:mm:ss")
                };
                getEarthquakeEventsForLast24hrs(json_data, response => {
                    response.map((row, index) => {
                        const { eq_id } = row;
                        const check_eq_id = all_alerts_from_db.filter(e => e.eq_id === eq_id);
                        if (check_eq_id.length === 0) {
                            temp_arr.push(row);
                        }
                    });

                    setEarthquakeEvents(temp_arr);
                });
            });
            
        }
    }, [site]);

    return (
        <Fragment>
            {
                selectSite && (
                    <div style={{ marginBottom: 12 }}>
                        <DynaslopeSiteSelectInputForm
                            value={site}
                            changeHandler={value => setSite(value)}
                        />
                    </div>
                )
            }
            {
                site !== null && (
                    <Grid container spacing={2}>
                        <Grid item lg={12} md={12} sm={12} xs={12}>
                            <FormControl fullWidth>
                                <InputLabel id="eq-event-select-label">Earthquake Event</InputLabel>
                                <Select
                                    labelId="eq-event-select-standard-label"
                                    id="eq-event-select-standard"
                                    value={eqId}
                                    onChange={handleEarthquakeEvent}
                                    label="Intensity"
                                >
                                    <MenuItem value="">
                                        <em>None</em>
                                    </MenuItem>
                                    {
                                        earthquake_events.length > 0 && (
                                            earthquake_events.map((row, index) => {
                                                const { eq_id, magnitude, ts, province } = row;
                                                const timestamp = moment(ts).format("YYYY-MM-DD HH:mm");
                                                return (
                                                    <MenuItem value={`${eq_id}`} key={index}>{timestamp} | {province}, Magnitude {parseFloat(magnitude)}</MenuItem>
                                                );
                                            })
                                        )
                                    }
                                </Select>
                            </FormControl> 
                        </Grid>
                        <Grid item lg={6} md={6} sm={12} xs={12}>
                            <DynaslopeUserSelectInputForm
                                variant="standard"
                                label="Reporter"
                                div_id="reporter"
                                changeHandler={event => setReporter(event.target.value)}
                                value={reporter}
                                isCommunityStaff="true"
                                site_code={site.data.site_code}
                            />
                        </Grid>
                        <Grid item lg={6} md={6} sm={12} xs={12}>
                            <FormControl fullWidth>
                                <InputLabel id="demo-simple-select-label">Intensity</InputLabel>
                                <Select
                                    labelId="demo-simple-select-standard-label"
                                    id="demo-simple-select-standard"
                                    value={intensity}
                                    onChange={handleIntensity}
                                    label="Intensity"
                                >
                                    <MenuItem value="">
                                        <em>None</em>
                                    </MenuItem>
                                    <MenuItem value="I">Intensity I</MenuItem>
                                    <MenuItem value="II">Intensity II</MenuItem>
                                    <MenuItem value="III">Intensity III</MenuItem>
                                    <MenuItem value="IV">Intensity IV</MenuItem>
                                    <MenuItem value="V">Intensity V</MenuItem>
                                    <MenuItem value="VI">Intensity VI</MenuItem>
                                    <MenuItem value="VII">Intensity VII</MenuItem>
                                    <MenuItem value="VIII">Intensity VIII</MenuItem>
                                    <MenuItem value="IX">Intensity IX</MenuItem>
                                    <MenuItem value="X">Intensity X</MenuItem>
                                </Select>
                            </FormControl>    
                        </Grid>
                        <Grid item lg={6} md={6} sm={12} xs={12}>
                            <TextField
                                id="remarks"
                                label="Remarks"
                                fullWidth
                                multiline
                                value={remarks}
                                onChange={handleRemarks}
                                variant="standard"
                            />

                        </Grid>
                        <Grid item lg={6} md={6} sm={12} xs={12}>
                            <MuiPickersUtilsProvider utils={MomentUtils}>
                                <KeyboardDateTimePicker
                                    required
                                    autoOk
                                    label="Timestamp Felt"
                                    value={tsFelt}
                                    onChange={handleTimestampFelt}
                                    ampm={false}
                                    placeholder="2010/01/01 00:00"
                                    format="YYYY/MM/DD HH:mm"
                                    mask="____/__/__ __:__"
                                    clearable
                                    disableFuture
                                    fullWidth
                                />
                            </MuiPickersUtilsProvider>
                        </Grid>
                    </Grid>
                )
            }
            
        </Fragment>
    );
}

export default EarthquakeIntensityForm;