import React, { useState, useEffect, useContext } from "react";

import {
    Dialog, DialogTitle, DialogContent,
    DialogContentText, DialogActions,
    Button, withMobileDialog,
} from "@material-ui/core";

import moment from "moment";
import { useSnackbar } from "notistack";

import EarthquakeIntensityForm from "./EarthquakeIntensityForm";
import { insertEqIntensity } from "./ajax";
import { GeneralContext } from "../../contexts/GeneralContext";
import { WebsocketContext } from "../../contexts/WebsocketContext"; 
import { getCurrentUser } from "../../sessions/auth";

function EarthquakeIntensityModal (props) {
    const {
        fullScreen, isOpen,
        closeHandler, match
    } = props;
    const { sites } = useContext(GeneralContext);
    const { alertsFromDbData } = useContext(WebsocketContext);

    const [site, setSite] = useState(null);
    const [site_code, setSiteCode] = useState(null);
    const [to_select_site, setToSelectSite] = useState(true);
    const [remarks, setRemarks] = useState("");
    const [reporter_id, setReporter] = useState("");
    const [eq_event, setEqEvent] = useState([]);
    const [intensity, setIntensity] = useState("");
    const [submit_button_state, setSubmitButtonState] = useState(true);
    const [eq_id, setEqId] = useState([]);
    const { user_id } = getCurrentUser();
    const [ts_felt, setTsFelt] = useState(moment().format("YYYY-MM-DD HH:mm:ss"));
    

    useEffect(() => {
        if (typeof match !== "undefined") {
            const { params: { site_code: sc } } = match;
            setToSelectSite(false);
            setSiteCode(sc);
        } 
    }, [match]);
    
    useEffect(() => {
        const row = sites.find(x => x.site_code === site_code);
        if (row) setSite({ value: row.site_id, data: row });
    }, [sites, site_code]);

    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const snackBarActionFn = key => (<Button
        color="primary"
        onClick={() => { closeSnackbar(key); }}
    >
        Dismiss
    </Button>);

    const clearFields = () => {
        setSite(null);
        setSiteCode(null);
        setToSelectSite(true);
        setSubmitButtonState(false);
        setRemarks("");
        setEqEvent("");
        setIntensity("");
    };

    useEffect(() => {
        const button_state = site === null || eq_id === "" || reporter_id === "" || intensity === "";
        setSubmitButtonState(button_state);
    }, [site, eq_id, intensity, reporter_id]);

    const handleSubmit = () => {
        closeHandler();

        const eq_intensity_data = {
            eq_id,
            reporter_id,
            intensity,
            remarks,
            user_id,
            site_id: site.value,
            ts_felt: moment(ts_felt).format("YYYY-MM-DD HH:mm:ss")
        };
        insertEqIntensity(eq_intensity_data, response => {
            const { status, message } = response;
            let variant = "success";
            if (status) {
                clearFields();
            } else {
                variant = "error";
            }

            enqueueSnackbar(
                message, 
                {
                    variant,
                    autoHideDuration: 4000,
                    action: snackBarActionFn
                }
            );
        });
    };

    return (
        <Dialog
            fullWidth
            fullScreen={fullScreen}
            open={isOpen}
            maxWidth="sm"
            aria-labelledby="form-dialog-title"
        >
            <DialogTitle id="form-dialog-title">Insert Earthquake Intensity Data Form</DialogTitle>
            <DialogContent>
                <DialogContentText>
                    Provide earthquake intensity details to save information in our database.
                </DialogContentText>

                <EarthquakeIntensityForm
                    site={site}
                    setSite={setSite}
                    selectSite={to_select_site}
                    setReporter={setReporter}
                    reporter={reporter_id}
                    remarks={remarks}
                    eqEvent={eq_event}
                    intensity={intensity}
                    setRemarks={setRemarks}
                    setEqEvent={setEqEvent}
                    setIntensity={setIntensity}
                    alertsFromDbData={alertsFromDbData}
                    eqId={eq_id}
                    setEqId={setEqId}
                    tsFelt={ts_felt}
                    setTsFelt={setTsFelt}
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={closeHandler} color="primary">
                    Cancel
                </Button>
                <Button 
                    onClick={handleSubmit}
                    color="primary"
                    disabled={submit_button_state}>
                    Submit
                </Button>
            </DialogActions>
        </Dialog>
    );
}

export default withMobileDialog()(EarthquakeIntensityModal);
