import React, { Fragment, useState, useContext, useEffect } from "react";
import {
    Grid, TextField,
    Radio, FormControl, FormLabel,
    RadioGroup, FormControlLabel, CircularProgress,
    Checkbox, InputLabel, Select, MenuItem
} from "@material-ui/core";

import MomentUtils from "@date-io/moment";
import { MuiPickersUtilsProvider, KeyboardDateTimePicker } from "@material-ui/pickers";

// Widgets
import moment from "moment";
import DynaslopeUserSelectInputForm from "../../reusables/DynaslopeUserSelectInputForm";
import DynaslopeSiteSelectInputForm from "../../reusables/DynaslopeSiteSelectInputForm";
import { getEarthquakeEventsForLast24hrs } from "./ajax";

function OnDemandForm (props) {
    const {
        selectSite, site, setSite,
        alertLevel, setAlertLevel,
        requestTs, setRequestTs,
        reason, setReason,
        reporter, setReporter,
        techInfo, setTechInfo,
        loading, hasOnDemand,
        isEq, setIsEq,
        earthquakeEvents, setEarthquakeEvents,
        earthquakeId, setEarthquakeId
    } = props;

    const initial_label = { label: "Reason", holder: "Enter reason (will appear on timeline)" };
    const [reason_label, setReasonLabel] = useState(initial_label);
    
    const handleChange = event => {
        const { value } = event.target;
        setAlertLevel(value);
        let label = initial_label;
        if (value === "0") {
            label = { label: "Remarks", holder: "Enter remarks (will appear on timeline)" };
            setTechInfo("");
        }
        setReasonLabel(label);
    };

    const handleIsEarthquake = event => {
        const { checked } = event.target;
        setIsEq(checked);
    };

    useEffect(() => {
        const json_data = {
            start_ts: moment().format("YYYY-MM-DD HH:mm:ss"),
            end_ts: moment().subtract(1, "days")
            .format("YYYY-MM-DD HH:mm:ss")
        };
        getEarthquakeEventsForLast24hrs(json_data, response => {
            console.log(response);
            setEarthquakeEvents(response);
        });
    }, []);

    return (
        <Fragment>
            {
                selectSite && (
                    <div style={{ marginBottom: 12 }}>
                        <DynaslopeSiteSelectInputForm
                            value={site}
                            changeHandler={value => setSite(value)}
                        />
                    </div>
                )
            }

            {
                loading && <Grid container justify="center"><CircularProgress /></Grid>
            }

            {
                site !== null && !loading && (
                    <Fragment>
                        <Grid container spacing={1}>
                            <Grid item xs={12} sm={12} md={6} lg={6}>
                                <FormControl component="fieldset" style={{ display: "flex", marginTop: 16 }}>
                                    <FormLabel component="legend" style={{ textAlign: "center", marginBottom: 8 }}>
                                        Choose alert level
                                    </FormLabel>

                                    <RadioGroup 
                                        aria-label="choose_alert_level"
                                        name="choose_alert_level"
                                        value={alertLevel}
                                        onChange={handleChange}
                                        row
                                        style={{ justifyContent: "space-around" }}
                                    >
                                        <FormControlLabel
                                            value="1"
                                            control={<Radio color="primary"/>}
                                            label="Raise/Extend (d1)"
                                            key="d1"
                                        />

                                        {
                                            hasOnDemand && <FormControlLabel
                                                value="0"
                                                control={<Radio color="primary"/>}
                                                label="Lower/End (d)"
                                                key="d"
                                            />
                                        }
                                    </RadioGroup>
                                </FormControl>
                            </Grid>
                            <Grid item xs={12} sm={12} md={6} lg={6} style={{ alignSelf: "center", textAlign: "center" }}>
                                <FormControlLabel control={<Checkbox 
                                    checked={isEq} 
                                    onChange={handleIsEarthquake}
                                />} label="Is Earthquake?" />
                            </Grid>
                        </Grid>

                        { isEq && (
                            <Grid item xs={12} sm={12} md={12} lg={12} style={{ alignSelf: "center", textAlign: "center" }}>
                                <FormControl fullWidth>
                                    <InputLabel id="eq-event-select-label">Earthquake Event</InputLabel>
                                    <Select
                                        labelId="eq-event-select-standard-label"
                                        id="eq-event-select-standard"
                                        value={earthquakeId}
                                        onChange={e => setEarthquakeId(e.target.value)}
                                        label="Intensity"
                                    >
                                        <MenuItem value="0">
                                            <em>None</em>
                                        </MenuItem>
                                        {
                                            earthquakeEvents.length > 0 && (
                                                earthquakeEvents.map((row, index) => {
                                                    const { magnitude, ts, province, eq_id } = row;
                                                    const timestamp = moment(ts).format("YYYY-MM-DD HH:mm");
                                                    return (
                                                        <MenuItem value={`${eq_id}`} key={index}>{timestamp} | {province}, Magnitude {parseFloat(magnitude)}</MenuItem>
                                                    );
                                                })
                                            )
                                        }
                                    </Select>
                                </FormControl> 
                            </Grid>
                        )
                        }
                        

                        <Grid container spacing={1}>
                            <Grid item xs={12} sm={6} md={6}>
                                <MuiPickersUtilsProvider utils={MomentUtils}>
                                    <KeyboardDateTimePicker
                                        required
                                        autoOk
                                        label="Timestamp"
                                        value={requestTs}
                                        onChange={val => setRequestTs(val)}
                                        ampm={false}
                                        placeholder="2010/01/01 00:00"
                                        format="YYYY/MM/DD HH:mm"
                                        mask="____/__/__ __:__"
                                        clearable
                                        disableFuture
                                        fullWidth
                                    />
                                </MuiPickersUtilsProvider>
                            </Grid>

                            {
                                alertLevel === "1" && (
                                    <Grid item xs={12} sm={6} md={6}>
                                        <TextField
                                            label="Technical Information"
                                            multiline
                                            rowsMax="2"
                                            placeholder="This will appear on the EWI bulletin"
                                            value={techInfo}
                                            onChange={event => setTechInfo(event.target.value)}
                                            fullWidth
                                            required
                                        />
                                    </Grid>
                                )
                            }
                            
                            <Grid item xs={12} sm={6} md={6}>
                                <TextField
                                    label={reason_label.label}
                                    multiline
                                    rowsMax="2"
                                    placeholder={reason_label.holder}
                                    value={reason}
                                    onChange={event => setReason(event.target.value)}
                                    fullWidth
                                    required
                                />
                            </Grid>

                            <Grid item xs={12} sm={6} md={6}>
                                <DynaslopeUserSelectInputForm
                                    variant="standard"
                                    label="Reporter"
                                    div_id="reporter"
                                    changeHandler={event => setReporter(event.target.value)}
                                    value={reporter}
                                    isCommunityStaff="true"
                                    site_code={site.data.site_code}
                                />
                            </Grid>
                        </Grid>
                    </Fragment>
                )
            }
            
        </Fragment>
    );
}

export default OnDemandForm;