import axios from "axios";
import { host } from "../../../config";

// eslint-disable-next-line import/prefer-default-export
export function getEarthquakeAlerts (callback) {
    const api_link = `${host}/api/analysis/get_earthquake_alerts`;

    axios.get(api_link)
    .then(response => {
        const { data } = response;
        console.log("EQ Alerts", data);
        callback(data);
    })
    .catch(error => {
        console.error(error);
    });
}

export function insertEqIntensity (eq_intensity_data, callback) {
    const api_link = `${host}/api/monitoring/save_earthquake_intensity`;

    axios.post(api_link, eq_intensity_data)
    .then((response) => {
        const { data } = response; 
        if (callback !== null) {
            console.log("EQ Intensity result", data);
            callback(data);
        } 
    })
    .catch((error) => {
        console.log(error);
    });
}